.emas__news {
    background-color: white;
    display: flex;
    flex-direction: column;
    padding: 8vw 20vw;
}

.emas__news-heading {
    text-align: left;
    color: black;
    padding-bottom: 0rem;
}

.emas__news-heading h1 {
    font-size: 35px;
    padding-bottom: 1rem;
}

.emas__news-heading h2 {
    padding-top: 2rem;
    font-size: 30px;
}

.emas__news-heading p {
    font-size: 18px;
}

.emas__news-content {
    border-top: 2px solid gray;
}

@media screen and (max-width: 414px) {
    .emas__news {
        background-color: white;
        display: flex;
        flex-direction: column;
        padding: 5rem 2rem;
    }

    .emas__news-heading h1 {
        font-size: 35px;
        padding-bottom: 0rem;
    }
}


