.emas__footer {
    padding: 10vw 14vw;
    display: flex;
    justify-content: space-around;

}


.emas__footer a {
    color: whitesmoke;
    padding: 1.5vw 0rem;
    font-size: 1.6vw;
    line-height: 3vw;
    font-weight: 600;
}

@media screen and (max-width: 414px) {
    .emas__footer {
        display: grid;
        grid-template-columns: auto auto;
        padding: 4rem 2rem;
        row-gap: 1rem;
    }
    .emas__footer a {
        font-size: 16px;
    }
}