.emas__eachnews {
    padding: 0rem 2vw;
    cursor: pointer;
    display: flex;
    flex-direction: row;
    border-bottom: 2px solid gray;
    height: 15vw;
    justify-content: space-between;
    
}

.emas__eachnews:hover {
    background-color: aliceblue;
}

.emas__eachnews-content {
    display: flex;
    flex-direction: column;
    padding-right: 2rem;
}

.emas__eachnews-content h1 {
    font-size: 1.2vw;
}

.emas__eachnews-content p {
    font-size: 0.9vw;
}

.emas__eachnews-content i {
    font-size: 0.9vw;
}

.emas__eachnews-image {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 2rem 0;
}

.emas__eachnews-image img {
    width: 18vw;
    height: 12vw;
}

@media screen and (max-width: 414px) {

    .emas__eachnews-content_description {
        display: none;
    }

    .emas__eachnews {
        padding: 0rem 1rem;
        height: 9rem;
    }

    .emas__eachnews-content {
        justify-content: center;
    }
    .emas__eachnews-content h1 {
        font-size: 16px;
    }

    .emas__eachnews-content i {
        font-size: 14px
    }

    .emas__eachnews-image {
        padding: 1rem 0;
    }

    .emas__eachnews-image img {
        height: 5rem;
        width: 7rem;
    }
}


